import React from 'react';
import Footer from '../../Components/Common/Footer';
import { Container } from '../../Components/Common/Container';
import img from '../../Assets/images/followImg/fog.jpg';
import img2 from '../../Assets/images/followImg/human.jpg';
import img3 from '../../Assets/images/followImg/woman2.jpg';
import { Link } from 'react-router-dom';
import Header from '../../Components/Common/Header/Header';
import {
  ChatListWrap,
  SearchWrap,
  SearchPhotoWrap,
  SearchImg,
  UserInfo,
  UserId,
  UserText,
  ChatDateBox,
  ChatDate,
} from './ChatStyle';

export default function ChatList() {
  return (
    <div>
      <Container>
        <Header type='moreKebab' />
        <ChatListWrap>
          <SearchWrap>
            <SearchPhotoWrap>
              <SearchImg src={img} alt='' />
            </SearchPhotoWrap>

            <Link to='/chat/kim'>
              <UserInfo className='unRead'>
                <UserId> </UserId>
                <UserText>  ~</UserText>
              </UserInfo>
            </Link>
            <ChatDateBox>
              <ChatDate>2023.10.25</ChatDate>
            </ChatDateBox>
          </SearchWrap>
          <SearchWrap>
            <SearchPhotoWrap>
              <SearchImg src={img2} alt='' />
            </SearchPhotoWrap>
            <Link to='/chat/sumiDad'>
              <UserInfo>
                <UserId> </UserId>
                <UserText>  ~</UserText>
              </UserInfo>
            </Link>
            <ChatDateBox>
              <ChatDate>2023.10.25</ChatDate>
            </ChatDateBox>
          </SearchWrap>
          <SearchWrap>
            <SearchPhotoWrap>
              <SearchImg src={img3} alt='' />
            </SearchPhotoWrap>
            <Link to='/chat/darkHorse'>
              <UserInfo>
                <UserId> </UserId>
                <UserText>   ?</UserText>
              </UserInfo>
            </Link>
            <ChatDateBox>
              <ChatDate>2020.10.25</ChatDate>
            </ChatDateBox>
          </SearchWrap>

          <Footer></Footer>
        </ChatListWrap>
      </Container>
    </div>
  );
}
